import React from 'react';
import { Select } from 'antd';
import likesData from './like.json'
const { Option } = Select;
const children = [];
const star = ['', '★', '★★', '★★★', '★★★★', '★★★★★']

let list = Object.keys(likesData)
for (let i = 0; i < list.length; i++) {
  let item = likesData[list[i]]
  children.push(<Option  key={list[i]}>
    {item.name}
    {star[item.star]}
  </Option>);
}



class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      history: [{
        squares: Array(9).fill(null)
      }],// 因为这个是一个数组，所以是可以往里push 的。。。 
      xIsNext: true,
      labelll: '12',
    }
  }


  render() {
    return (
      <div className="game">
          {/* optionFilterProp="children"
          showSearch
          filterOption={(input, option) => option.children.includes(input)} */}
        <Select
          placeholder="输入魔物娘"
          style={{ width: '50%'}}
          onChange={(value)=>this.handleChange(value)}
        >
          {children}
        </Select>
        <br/>
        <span>{this.state.labelll}</span>
      </div>
    );
  }
  handleChange = (value) => {
    let str=[]
    likesData[value].likes.map(item=>{
      str.push(<span>{item}<br></br></span>)//item+str;
    })
    this.setState({labelll:str})
  };
}

export default App;

